.intro,
.logo {
    left: 50%;
    opacity: 0;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    z-index: 200;
}

.intro p {
    color: rgb(75, 213, 238);
    font-size: 1.25rem;
}

.logo {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 18rem;
}

.crawl {
    font-size: 100%;
    bottom: 0;
    height: 80rem;
    left: 50%;
    position: absolute;
    transform: translateX(-50%) perspective(300px) rotateX(28deg);
    transform-origin: 50% 100%;
    width: 90%;
}

.content {
    position: absolute;
    top: 100%;
    z-index: -1000;
    height: 100%;
}

.title {
    font-weight: 600;
    margin-bottom: 5rem;
    text-align: center;
    color: #e2e2e2;
}

.subtitle {
    font-family: 'Saira Extra Condensed', sans-serif;
    font-size: 250%;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 7rem;
    transform: scale(1, 1.5);
    text-align: center;
    color: #e2e2e2;
}

.content p {
    font-weight: 700;
    line-height: 1.33;
    margin-bottom: 4rem;
    text-align: center;
    color: yellowgreen;
}

.volume {
    background: transparent;
    border: 0;
    bottom: 10px;
    cursor: pointer;
    left: 10px;
    position: absolute;
    z-index: 1000;
}

.volume img {
    height: 24px;
}
